.banner2 {
    background-image:url('/images/banner_bg.png');
    background-color: #32ab85;
    position: relative;
    top: 0px;
    z-index: 0;
    display: flex;
    width: 100%;
    align-content: center;
    align-items: center;
    color: #fff;
    max-height: 100px;

}

.auxiliar{
    height: 100px;
    max-height: 100px;
}
.banner2 figure {
    padding-top: 0px;
    margin-bottom: 0px;
}

.banner2 figure img {
    width: 100%;
    height: 100px;
}

.banner2 p {
    color: #fff;
    font-size: 1.6em
}


@media (max-width:1200px) {
    .banner2 p {
        font-size: 1em;
    }

    .banner2 figure img {
        max-width: 489px;
        width: 100%;
    }
}

@media screen and (max-width:767px) {
    .banner2 figure {
        padding-top: 108px;
    }

    .banner2 figure img {
        max-width: 500px;
        width: 100%;
    }
}


@media screen and (max-width:580px) {
    .banner2 figure img {
        max-width: 80%;
    }
}