.appointment {
  /* f0da */
}
.appointment .react-tabs__tab {
  position: relative;
}
.appointment li#react-tabs-0,
.appointment li#react-tabs-0 a {
  background-color: inherit;
  color: inherit;
}
.appointment .progtrckr .progtrckr-doing::before {
  content: '\F0DA';
  position: absolute;
  right: -10px;
  top: 0px;
  font-family: 'FontAwesome';
  color: #34a2d7;
  font-size: 36px;
}
.appointment .react-tabs__tab.react-tabs__tab--selected {
  background-color: #34a2d7 !important;
  color: #fff !important;
}
.appointment .progtrckr {
  list-style: none;
  padding: 0;
  background: #eff0f0;
}

.appointment .progtrckr .progtrckr-doing,
.appointment .progtrckr .progtrckr-done {
  background-color: #34a2d7;
  color: #fff;
  position: relative;
}
.appointment .progtrckr li {
  display: inline-block;
  padding: 13px 30px;
  height: 50px;
}
.appointment .progtrckr li:first-child em {
  display: none;
}
.appointment .progtrckr li em {
  font-style: normal;
  font-size: 0;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: inline-block;
  text-align: center;
  line-height: 20px;
  background: #9faeac;
  color: #fff;
  margin-right: 12px;
  position: relative;
  top: -6px;
}
.appointment .progtrckr li:nth-child(2) em::before {
  content: '1';
  position: absolute;
  font-size: 17px;
  left: 7px;
  font-weight: bold;
}
.appointment .progtrckr li:nth-child(3) em::before {
  content: '2';
  position: absolute;
  font-size: 17px;
  left: 7px;
  font-weight: bold;
}
.appointment .progtrckr li:nth-child(4) em::before {
  content: '3';
  position: absolute;
  font-size: 17px;
  left: 7px;
  font-weight: bold;
}
.step1 {
  padding: 18px 0 0;
}

.appointment .progtrckr li {
  color: #9faeac;
  background-color: #eff0f0;
  border-right: 4px solid #fff;
  text-decoration: none;
  font-style: normal;
}
.type-of-appointment li {
  display: inline-block;
  max-width: 200px;
  vertical-align: top;
  text-align: center;
  margin-right: 12px;
  margin-bottom: 20px;
}
.type-of-appointment figure {
  padding: 63px 20px 0px;
  box-shadow: 1px 1px 2px 1px #ececec;
  border: 1px solid #e8e8e8;
  border-radius: 5px;
  margin-bottom: 30px;
  margin-top: 45px;
}
.type-of-appointment figure p {
  font-size: 24px;
  color: #565d5c;
}
.type-of-appointment figure span {
  width: 90px;
  height: 90px;
  background: #09a5e2;
  display: inline-block;
  border-radius: 50%;
  color: #fff;
  line-height: 92px;
  font-size: 30px;
  margin-bottom: 12px;
}
.type-of-appointment figcaption a {
  color: #09a5e2;
  text-decoration: none;
}
.type-of-appointment figcaption i {
  color: #cacaca;
  font-weight: bold;
  margin-bottom: 0;
}
.type-of-appointment figcaption p {
  margin-bottom: 5px;
  font-size: 16px;
}
.step1 h2 {
  color: #34a2d7;
  font-size: 40px;
  margin-bottom: 32px;
}
.appointment .progtrckr .progtrckr-doing em,
.appointment .progtrckr .progtrckr-done em {
  background-color: #fff;
  color: #9faeac;
}
.step1 h5 {
  font-size: 28px;
  margin: 35px 0 0;
}

/* step2 */
.step2 {
  padding: 20px 0;
}

.step2 .login-panel {
  background-image: none !important;
  background-color: #f7f7f7;
  width: 95%;
}
.step2 .login-panel .login {
  height: 100%;
  top: 0;
  bottom: 0;
}
.step2 .signup.text-left {
  color: #717171;
  padding: 60px 0px;
}
.step2 .signup.text-left h1 {
  color: #1ba48a;
  font-size: 28px;
}
.step2 .signup button.btn.btn-filter {
  background-color: #1ba48a;
  color: #fff;
  max-width: 100%;
  border-color: #1ba48a;
}
.step2 .login .ribban {
  filter: gray; /* IE6-9 */
  -webkit-filter: grayscale(100%);
}
.step2 .logcontent .title {
  color: #09a5e2 !important;
}
.step2 .logcontent .btn-success {
  background-color: #09a5e2;
  border-color: #09a5e2;
}

.step4 .details,
.step4 .card {
  padding: 55px 50px;
}
.step4 {
  margin-bottom: -61px;
}
.step4 .details i {
  margin-right: 12px;
  font-size: 25px;
}
.step4 .details h4.text-primary {
  font-size: 25px;
  margin-bottom: 24px;
}
.step4 .details .media {
  margin-bottom: 12px;
}
.step4 .details label {
  font-weight: bold;
  display: block;
  margin: 0;
}
.step4 .details figure {
  width: auto;
  min-width: 120px;
  border: 6px solid #ffff;
  margin: 30px 0;
  display: inline-block;
}
.step4 .cencellation {
  background-color: #f7f7f7;
  width: 95%;
  margin: 20px 0;
}
.step4 form.form {
  padding: 24px 0 0;
  margin: 20px 0 0;
  border-top: 2px solid #f7f7f7;
}
.step4 form.form .add-another {
  padding: 24px 0;
  margin: 20px 0;
  border-top: 2px solid #f7f7f7;
  border-bottom: 2px solid #f7f7f7;
}
.step4 form.form .btn-success {
  background-color: #1ba48a;
}
.step4 form.form .btn {
  margin-bottom: 12px;
}
.step4 .thankyou {
  width: 90%;
}
.step4 .thankyou .content p img {
  margin-top: 16px;
  max-width: 120px;
}
.step4 .thankyou .content p {
  font-size: 15px;
}
.step4 .thankyou .content h1 {
  font-size: 25px;
}
.step4 figure.mobile_demo img {
  width: 100%;
  height: 100%;
}
.footer-buttons {
  position: absolute;
  bottom: 0;
}
.fee {
  padding-bottom: 94px;
}
.footer-buttons .btn {
  margin-right: 12px;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  padding: 0;
  min-width: 120px;
}
.step3.step4 {
  padding: 27px 0 30px;
}
.step3.step4 > h2.text-center.text-primary {
  font-size: 37px;
}
.step2 .signup p {
  color: #717171;
}
.step2 .signup .btn-primary,
.step2 .signup .btn-danger {
  font-size: 18px;
  height: 40px;
}
.step2 .signup .btn-primary i,
.step2 .signup .btn-danger i {
  font-size: 18px; /* height: 40px; */
  margin-top: -4px;
}

@media (max-width: 1380px) {
  .appointment .top-search-panel {
    padding: 0 15px;
  }
  .appointment .therapist-profile {
    padding: 40px 15px 60px;
  }
  .appointment .expert {
    right: 13px;
  }
  .step1 h2 {
    font-size: 33px;
  }
  .step2 .login {
    width: 100%;
  }
  .step2 .signup .btn-primary,
  .step2 .signup .btn-danger {
    font-size: 12px;
  }
  .appointment .progtrckr li {
    padding: 11px 30px;
    height: 44px;
  }
  .appointment .progtrckr .progtrckr-doing::before {
    top: -5px;
  }
  .step1 h5 {
    font-size: 20px;
  }
}
@media (max-width: 1200px) {
  .appointment .progtrckr li {
    padding: 8px 15px;
  }
  .step4 h2 {
    font-size: 25px;
  }
}

@media (max-width: 991px) {
  .appointment .expert {
    right: -271px;
  }
  .appointment .expert.open {
    right: 0;
  }
  .step4 .cencellation {
    width: 100%;
  }
  .step2 .login-panel {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .appointment .progtrckr {
    background-color: transparent;
  }
  .appointment .progtrckr li {
    margin-bottom: 5px;
  }
}
@media (max-width: 479px) {
  .step4 .details,
  .step4 .card {
    padding: 40px 15px;
  }
}

loader_circle: {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
