.alert-detail-signup {
  padding: 0 10px;
  display: inline-block;
  word-wrap: break-word;
  width: 85%;
  vertical-align: middle;
  font-size: 16px;
  white-space: pre-wrap;
  line-height: 25px;
}
.error-message-box {
  position: fixed;
  top: 74px;
  right: 20px;
  background-color: red;
  color: #ffffff;
  height: 68px;
  width: 355px;
  border: 2px solid #ff0000;
  border-radius: 3px;
  z-index: 1000000;
}
.alert-message-box {
  position: fixed;
  top: 74px;
