.modalOne {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modalOne-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

  .Vd-modal {
    z-index: 999;
}

.Vd-modal section.modalOne-main {
    border-radius: 6px;
    padding: 48px;
    width: 60%;
}

.List_left {
    text-align: left;
}

span.text_bx {
    font-family: 'Hind Madurai', sans-serif !important;
    font-size: 29px;
    color: #525252;
    width: 51%;
}

.List_left ul li {
    padding: 35px 20px;
    border-bottom: 1px solid #d8d8d8 !important;
    display: flex;
    flex-flow: wrap;
}

.List_left ul li:last-child {
    border-bottom: 0 !important;
    padding-bottom: 0;
}
.List_left ul {
    margin: 0;
}

span.imgbx {
    width: 49%;
    text-align: right;
}

.List_left ul li:first-child {
    padding-top: 0;
}

button.Close_Popup {
    outline: none;
    outline: none !important;
    cursor: pointer;
    background: transparent;
    border: 0;
    height: auto;
    position: absolute;
    width: auto;
    top: -13px;
    right: -11px;
    margin: 0;
}

button.Close_Popup img {
    width: 30px;
}


.report-bx h3 {
    color: #565d5cfc;
    font-size: 29px;
    margin: 0;
}

.report-bx h5 {
    color: #565d5c85;
    font-size: 14px;
    margin: 0;
    margin-bottom: 25px;
    padding: 11px 0;
}

.report-bx textarea {
    min-height: 250px;
    border: 1px solid #d8d8d8;
    border-radius: 4px;
    width: 100%;
    padding: 10px 0 0 20px;
    resize: vertical;
    outline: none !important;
    color: #000;
    background: #fff !important;
}

.report-bx textarea::placeholder {
    color: #8c8c8c;
    font-weight: 100;
}

.report_form form {
    max-width: 500px;
    margin: auto;
    background: #f9f9f9;
    padding: 25px;
}