.banner {
    /* background-image:url('/images/banner_bg.png'); */
    background-color: #32ab85;
    position: relative;
    top: -153px;
    z-index: 0;
    min-height: 694px;
    display: flex;
    width: 100%;
    align-content: center;
    align-items: center;
    color: #fff;
    background-size: cover;

}

.banner figure {
    padding-top: 75px;
    margin-bottom: 20px;
}

.banner figure img {
    max-width: 763px;
    width: 100%;
}

.banner p {
    color: #fff;
    font-size: 1.6em
}


@media (max-width:1200px) {
    .banner p {
        font-size: 1em;
    }

    .banner figure img {
        max-width: 489px;
        width: 100%;
    }
}

@media screen and (max-width:767px) {
    .banner figure {
        padding-top: 108px;
    }

    .banner figure img {
        max-width: 500px;
        width: 100%;
    }
}


@media screen and (max-width:580px) {
    .banner figure img {
        max-width: 80%;
    }
}