.type-therapy .search-panel{ padding:30px 0 0; background:#f4f8f6;}
.type-therapy .advance-searh a{ color:#0ea092 !important}
.type-therapy .breadcum{padding:6px 0; background-color:#f4f8f6;}
.type-therapy .breadcum ul{margin:0;}
.type-therapy .breadcum ul .nav-links{color: #6d6a6a;}
.therapy-content { margin-bottom: 70px;}
.type-therapy .breadcum ul .nav-links::after{color: #0ea092;}
.intro-therapy{padding:60px 0 40px;}
.type-therapy .content p{color:#7c8482;}
.type-therapy .content{margin-bottom:30px;margin-top:40px;}
.type-therapy .App-section .content p{ color: #fff;}
.type-therapy .top-banner {  margin-top: -17px;}

@media screen and (max-width:991px){
    .therapy-content p{font-size:15px; line-height:24px;}
    .type-therapy .top-banner h1 { font-size: 40px;}
    .type-therapy .App-section .content h1 { font-size: 25px;}
   .type-therapy .App-section .content img { max-width:120px; }
}
@media screen and (max-width:479px){
    .type-therapy .top-banner h1 { font-size: 30px;}
    .therapy-content h3 { font-size: 20px;}
    .intro-therapy { padding: 20px 0 10px;}
    .type-therapy .content { margin-bottom: 20px;  margin-top: 27px;}
}