footer{  background:#fff;    padding: 102px 5% 75px;  color:#0ea092;}
footer.HJ_home, footer.HJ_search,footer.HJ_therapist,footer.HJ_appointment{  background:#0ea092; color:#fff;}
footer.HJ_home p, footer.HJ_search p,footer.HJ_therapist p,footer.HJ_appointment p{  color:#fff;}
footer p{color:#0ea092;}
footer p{padding-left:50px; max-width:600px; }
footer .social-icon{padding-left:50px; }
footer .social-icon li{ list-style:none; display: inline;}
footer ul{padding:0; list-style:none;}
footer.HJ_home a, footer.HJ_search a, footer.HJ_therapist a, footer.HJ_appointment a{ color:#aeded9; font-weight:lighter;}
footer a{ color:#0ea092; font-weight:lighter;}
footer a:hover{ color:#8cc975; text-decoration:none}
footer h4{font-size:24px;    margin-bottom: 22px;}
.copyright{    padding: 35px 5% 0; background:#fff; color:#333; padding-left:5%;  box-shadow: inset 3px 7px 13px -9px #e2e2e2; }
footer figure img {  max-width: 320px;  width: 100%;}
footer ul li { margin-bottom: 10px;   font-size: 17px;}

@media screen and (max-width:1480px){
footer h4 {  font-size: 20px;}
}
@media screen and (max-width:991px){
  footer h4 { font-size: 18px;}
  footer img{max-width:100%;}
  footer a {  font-size: 13px;}
  footer p { font-size: 10px;}
  footer ul li {   margin-bottom: 0px;}
}

@media screen and (max-width:767px){
  footer h4 { font-size: 22px;}
  footer img{max-width:100%;}
  footer a {  font-size:15px;}
  footer p { font-size: 13px;}
  footer .social-icon {  margin-bottom: 30px;  padding-bottom: 20px;  border-bottom: 1px solid rgba(194, 222, 219, 0.38823529411764707);}
  .copyright p {  font-size: 14px;}
}