.about-search{padding-top:40px; background-color:#f4f8f6; }
.about-search p a {  color: #11a18c !important;}
.search-intro{padding:83px 0 0;}
.search-intro h1.title{font-size:46px;}
.search-intro .sub-title{    padding: 0px 0 20px;max-width: 920px;margin: 0 auto;letter-spacing: 0.5px;word-spacing: 2px;line-height: 35px;    margin-bottom: 12px;}
.search-intro p{padding:0px 0 0px;}
.search-content .items{text-align:center; margin-bottom:48px;}
.search-content .items figure{height: 180px; width: 180px; border-radius: 50%; background-color: #c3c3c3;  margin: 20px auto 50px;    border: 10px solid #fff;}
.search-content p{font-size:18px; color:#7c8482;}
.search-content a{font-size:22px; color:#7c8482;}
.search-content .title{font-size:28px; margin-bottom: 6px;}
.search-content {position: relative; padding:60px 0;  } 
.search-content::after{    content: '';position: absolute;width: 100%;bottom: 0;left: 0;height: calc(100% - 180px);background-color: #f4f8f6;z-index: -1; }
.map-cotainer{height: 164px;  position: relative;;}

@media screen and (max-width:1380px){

    .search-content .title { font-size: 20px;}
    .search-content p {  font-size: 15px;}
}

@media screen and (max-width:991px){
    .search-intro .sub-title {  font-size: 18px;    line-height: normal;}
    .search-intro p {  padding: 0px 0 0;  font-size: 14px;}
    .search-content .items { max-width: 279px;  margin: 0 auto;    position: relative; padding-bottom:12px;}
    .search-content .items::after {content: '';position: absolute;width: 100%;height: calc(100% - 59px);background: #f5f5f5;bottom: 0;  left: 0; z-index: -1; }
    .search-content::after{display:none;}
}

@media screen and (max-width:767px){
    .search-intro h1.title { font-size: 30px;}
    .search-intro {   padding: 35px 0 0;}
    .search-content { position: relative;  padding: 10px 0;}
    
}