.wellness { padding:48px 0 89px; }
.wellness figure{margin: 0 auto;padding: 12px;height: 125px;width: 125px;display: flex;align-items: center;background: #f1f1f1;border-radius: 50%; }
.wellness figure img { margin: 0 auto; width: 55px; height: auto;}
.wellness h3{margin:60px 0 12px; font-size:26px;}

.support-need{ position: relative;     margin-top: -63px;}
.support-need .title {
    font-size: 46px;
}
.support-need  .splt-devider span img{ width:100%;}
.support-need  .splt-devider span{     background: #fff;
    display: inline-block;
    padding: 27px;
    width: 101px;
    height: 106px;
    border-radius: 50%;}
.support-need .splt-devider {  position: absolute;        top: -129px;
    width: 100%;}
.providers{ background-color:#f3f7f5; padding:60px 0 70px; position: relative;}
.provider-slider figure{ height:166px; width:166px; border-radius:50%; background-position:center; background-size:cover; margin:20px auto 11px; background-repeat:no-repeat; }
.provider-slider .card{     min-height: 325px;    width: calc(100% - 20px) !important;}
.provider-slider figcaption{    padding: 20px 30px 10px; text-align:left;}
.provider-slider figcaption h1{font-size:18px; color:#34a2d7;}
.provider-slider figcaption p{font-size:16px; color:#7c8482; margin:0;}
.provider-slider figcaption .star{     padding-top: 29px;  padding-bottom: 7px;}
.provider-slider p i{ color:#ffd203}
.providers .title{font-size: 46px; margin:0}
.providers .title + p{    font-size: 21px;
    letter-spacing: 2px;}
.provider-slider .provider-slider {  margin-bottom: 30px;}
.provider-slider .slick-prev { left: -55px;}
.provider-slider .slick-next { right: -52px;}
.provider-slider .slick-next,.provider-slider .slick-next:hover, .provider-slider .slick-prev, .slick-prev:hover{    height: 45px;    width: 45px;    border-radius: 50%;    background: rgb(150,205,115);
background: -moz-linear-gradient(left, rgb(150,205,115) 1%, rgb(10,158,141) 55%, rgb(10,158,141) 55%, rgb(10,158,141) 87%);
background: -webkit-linear-gradient(left, rgb(150,205,115) 1%,rgb(10,158,141) 55%,rgb(10,158,141) 55%,rgb(10,158,141) 87%);
background: linear-gradient(to right, rgb(150,205,115) 1%,rgb(10,158,141) 55%,rgb(10,158,141) 55%,rgb(10,158,141) 87%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#96cd73', endColorstr='#0a9e8d',GradientType=1 );}
.provider-slider .slick-next:before, .provider-slider .slick-prev:before {     font-size: 30px;}
.provider-slider .slick-prev:before {content:'\f104'; font-family:'FontAwesome'}
.provider-slider .slick-next:before {content:'\f105'; font-family:'FontAwesome'}
.providers .pvtl-devider img{width:100%; height:auto;}
.providers .pvtl-devider { top: -46px;  overflow: hidden;  position: absolute;  background-position: center;  margin: 0; width:100%; }
.provider-slider {     width: 95%; margin:33px auto 36px;}


.new-feeds{ padding:80px 0 68px; }
.new-feeds .title{font-size:46px;}
.new-feeds .card{     padding: 29px 20px;
    margin-bottom: 20px;
    margin-top: 6px;}
.new-feeds .card img{width:100%;}
.new-feeds .card figcaption h5{ font-size:16px;}
.new-feeds .card figcaption h4{ font-size:22px; color:#34a2d7;}
p { font-size: 18px;  font-weight: 100;}
.viewAll { font-size: 20px;  font-weight: 500;}
.new-feeds .card figcaption p{font-size: 16px;}
.new-feeds figcaption {  min-height: 206px;}

 @media screen and (max-width:991px){
    .provider-slider { width: 85%; margin: 0 auto;}
    .app-banner .content p img {  margin-top: 30px;  max-width: 120px;}
    .providers .pvtl-devider {  top: -34px; }
    .support-need .title {
    font-size:30px;
}
        .new-feeds .card figcaption h4, .provider-slider figcaption h1,.wellness h3 {   font-size: 18px;}
 }
 @media screen and (max-width:767px){
   .providers .pvtl-devider { top: -14px; }
   footer .social-icon,footer p { padding-left: 0;}
   .provider-slider {width: 100%;}
   div.copyright { padding-left: 5%;  text-align: center !important;}
   .provider-slider .slick-prev { left: 20px; z-index: 999;  top: 43%;}
   .provider-slider .slick-next { right: 20px; z-index: 999;  top: 43%;}
   .provider-slider .slick-prev, .provider-slider .slick-next{display:none !important;}
   .provider-slider:hover .slick-prev, .provider-slider:hover .slick-next{display:block !important;}
   .providers .title{font-size:30px;}
 }