* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: sans-serif;
    font-size: 16px;
    color: #444;
  }
  
  #calendar {
    width: 640px;
    height: 480px;
    margin: 100px auto;
  }
  
  .Calendar-grid {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  
  .Calendar-header {
    height: 50px;
    background: #333;
    color: #fff;
    text-align: center;
    line-height: 50px;
    display: flex;
    font-size: 20px;
    justify-content: space-between;
  }
  
  .Calendar-header button {
    width: 50px;
    font-size: 20px;
    border: 0;
    background: transparent;
    color: #ddd;
    cursor: pointer;
  }
  
  .Calendar-grid-item {
    flex: 0 14.28571%;
    text-align: center;
    border-right: 0px solid #ddd;
    border-bottom: 0px solid #ddd;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .Calendar-grid-item.nextMonth,
  .Calendar-grid-item.prevMonth {
    color: #999;
  }
  
  .Calendar-grid-item:nth-child(7n+1) {
    border-left: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(7n) {
    border-right: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(-n+7) {
    border-top: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(5*7-7n) {
    border-bottom: 1px solid #ddd;
  }


  .Calendar-grid-item:nth-child(29) {
    border-bottom: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(30) {
    border-bottom: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(31) {
    border-bottom: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(32) {
    border-bottom: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(33) {
    border-bottom: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(34) {
    border-bottom: 1px solid #ddd;
  }
  .Calendar-grid-item:nth-child(35) {
    border-bottom: 1px solid #ddd;
  }

  