header2 {
    /* background:#ddd; */
    /* background:url('/images/header_bg.png'); */
    position: relative;
    z-index: 1;
    background-repeat: no-repeat;
    background-size: cover;
}

header2 .navbar {
    background-color: transparent !important;
    color: #fff;
    padding-bottom:0px;
    padding-right: 15%;
    padding-left: 15%;
    position: relative;
    width: 100%;
    align-items: center
}

header2.HJ_home a.navbar-brand,
header2.HJ_ a.navbar-brand {
    display: none
}

header2 a.navbar-brand {
    width: auto;
    height: 54px;
    padding: 0;
    vertical-align: middle;
    display: inherit;
}

header2 a.navbar-brand img {
    width: auto;
    height: 100%;
}

header2 .navbar-dark .navbar-nav .nav-link,
header2 .navbar-dark .navbar-nav .nav-link {
    color: #fff;
}

header2 .navbar-dark .navbar-nav .nav-link:hover {
    color: #8cc975;
}

header2 .navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 20px;
    padding-left: 18px;
    font-size: 18px;
}

header2 .navbar-light .navbar-nav .active>.nav-link {
    color: #fff;
    position: relative;
    font-size: 20px;
}

header2 .nav-link.dropdown-toggle:hover+.dropdown-menu,
.dropdown-menu:hover {
    display: block !important;
}

a.btn.btn-white.active {
    background-color: #fff !important;
    border-color: #fff !important;
}

header2 .dropdown-menu {
    padding: 0;
    background-color: rgba(255, 255, 255, 0.8901960784313725);
}

header2 .dropdown-menu a,
header2.HJ_search .navbar-dark .navbar-nav .nav-link:hover,
header2.HJ_therapist .navbar-dark .navbar-nav .nav-link:hover,
header2.HJ_appointment .navbar-dark .navbar-nav .nav-link:hover {
    color: #8cc975;
}

header2 .dropdown-menu a:hover,
header2 .dropdown-menu a:focus {
    color: #fff;
    background-color: #8cc975
}

header2 .navbar-dark .btn {
    padding-right: 20px;
}

header2.HJ_login,
header2.HJ_therapist,
header2.HJ_appointment {
    background: none !important;
}

header2.HJ_login .navbar-dark .navbar-nav .nav-link,
header2.HJ_login .navbar-dark .navbar-nav .nav-link,
header2.HJ_search .navbar-dark .navbar-nav .nav-link,
header2.HJ_search .navbar-dark .navbar-nav .nav-link,
header2.HJ_therapist .navbar-dark .navbar-nav .nav-link {
    color: #1ba48a;
}

header2.HJ_login .navbar-dark .btn,
header2.HJ_search .navbar-dark .btn,
header2.HJ_therapist .navbar-dark .btn,
header2.HJ_appointment .navbar-dark .btn {
    background-color: #34a2d7 !important;
    color: #fff !important;
}

header2.HJ_search .navbar-dark .navbar-nav .nav-link,
header2.HJ_login .navbar-dark .navbar-nav .nav-link,
header2.HJ_therapist .navbar-dark .navbar-nav .nav-link,
header2.HJ_appointment .navbar-dark .navbar-nav .nav-link {
    color: #333;
}

header2.HJ_login .navbar-expand-lg .navbar-collapse,
header2.HJ_search .navbar-expand-lg .navbar-collapse,
header2.HJ_therapist .navbar-expand-lg .navbar-collapse,
header2.HJ_appointment .navbar-expand-lg .navbar-collapse {
    justify-content: flex-end;
}

header2.HJ_login .navbar-expand-lg .navbar-collapse .mr-auto,
header2.HJ_search .navbar-expand-lg .navbar-collapse .mr-auto,
header2.HJ_therapist .navbar-expand-lg .navbar-collapse .mr-auto,
header2.HJ_appointment .navbar-expand-lg .navbar-collapse .mr-auto {
    margin-right: 0 !important;
}

.userlogin .dropdown-toggle:hover+.dropdown-menu {
    display: block !important;
}

.userlogin {
    display: flex;
    text-align: left;
}

.userlogin a:hover {
    text-decoration: none;
}

.userlogin .dropdown-toggle img {
    height: 60px;
    width: 60px;
    border-radius: 50%;
}

header2.loginheader2 {
    background-color: #fff;
    background-image: none !important;
}

header2.loginheader2 .navbar-nav .nav-link {
    color: #4f5655;
}

header2.loginheader2 .navbar-nav .nav-link.active {
    color: #059685;
}

.userlogin figure.img-rounded.media-left {
    margin-right: 12px;
    padding-left: 20px
}

.userlogin small {
    color: #a7a7a7;
}

.userlogin h6 {
    margin: 0;
    font-size: 22px;
    line-height: 21px;
    margin-top: 5px;
}

.userlogin .dropdown-toggle::after {
    position: absolute;
    right: -3px;
    top: calc(50% - 20px);
}

header2.loginheader2 div#navbarSupportedContent {
    justify-content: flex-end;
}

header2.loginheader2 ul.navbar-nav.mr-auto {
    margin-right: 16px !important;
}

.notification .dropdown-toggle::after {
    display: none;
}

.notification ul li a {
    padding: 6px 20px !important;
    display: block;
    border-bottom: 1px solid #ebebeb;
    color: #333;
}

/* .notification ul li:nth-child(odd) a {background-color:#fafafa;} */
.notification ul li a.read {
    opacity: 0.5;
}

.notification ul li a:hover {
    text-decoration: none
}

.notification .dropdown-menu {
    min-width: 280px;
}

.notification ul li a small {
    float: right;
    font-size: 12px;
    opacity: 0.5;
}

span.count-msg {
    position: absolute;
    font-size: 10px;
    color: #fff;
    width: 20px;
    height: 20px;
    background: #fe7b7b;
    line-height: 20px;
    border-radius: 50%;
    top: 0;
    font-weight: 100;
}

.billing-setting .card .media-right {
    font-size: 20px;
    color: #21a58b;
}

.billing-setting .card .media-right label {
    padding-right: 40px;
}

.billing-setting .card .media {
    align-items: center;
}

.billing-setting .card .media-body {
    margin-top: 0;
}

.loginheader2 .navbar {
    padding: 25px 5% 11px;
}








@media screen and (max-width:1380px) {
    header2 .navbar {
        padding: 20px;
    }

    header2 a.navbar-brand {
        height: 40px;
    }

    header2 .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: 0.5rem;
        padding-left: 1.5rem;
    }
}

@media (max-width:1200px) {
    header2 .navbar-expand-lg .navbar-nav .nav-link {
        padding-right: 16px;
        padding-left: 8px;
        font-size: 15px;
    }
}

@media screen and (max-width:991px) {
    div#navbarSupportedContent {
        width: 100%;
        position: absolute;
        background: rgba(5, 150, 133, 0.73);
        padding: 18px 25px;
        top: 78px;
        left: 0;
    }

    header2 .dropdown-menu {
        padding: 0;
        background-color: rgb(5, 150, 133);
        border: 0;
        padding: 0 30px;
        border-radius: 0;
    }

    header2 .navbar-dark .navbar-nav .nav-link,
    header2 .navbar-dark .navbar-nav .nav-link {
        text-align: left;
    }

    div#navbarSupportedContent {
        padding: 18px 0;
    }

    header2 form.form-inline.my-2.my-lg-0.dropdown {
        padding-left: 20px !important;
        padding: 0 22px;
    }

    header2.HJ_login .navbar-dark .navbar-nav .nav-link,
    header2.HJ_search .navbar-dark .navbar-nav .nav-link,
    header2.HJ_therapist .navbar-dark .navbar-nav .nav-link,
    header2.HJ_appointment .navbar-dark .navbar-nav .nav-link {
        color: #ffffff;
    }

    header2.HJ_login .navbar-dark .navbar-toggler,
    header2.HJ_search .navbar-dark .navbar-toggler,
    header2.HJ_therapist .navbar-dark .navbar-toggler,
    header2.HJ_appointment .navbar-dark .navbar-toggler {
        background: #059685;
    }

    header2.loginheader2 button.navbar-toggler {
        background-color: #0ea092;
    }

    header2.loginheader2 .nav-item .nav-link {
        color: #fff;
        font-size: 20px;
        padding: 6px 30px;
    }
}

@media screen and (max-width:479px) {
    header2 a.navbar-brand {
        height: 25px;
    }
}
