.profile-sidebar {  position: absolute;  height: 100%; max-width: 300px; width: 100%; background: #0ea092; color: #fff;box-shadow: 0px 5px 6px -1px #b3b3b3; overflow:auto;}
.profile-sidebar ul {    position: relative; height: 100%; vertical-align: top; display: block; padding-left: 5px;    padding-right: 0;padding-top: 23px;}
.profile-sidebar ul li{display:block; position: relative;}
.profile-sidebar ul li a{ display: block; text-decoration:none; width: 100%; padding: 12px 0px; text-align: left; color:#f4f8f6; padding-left:10px; border-top-left-radius:7px; border-bottom-left-radius:7px; }
.profile-sidebar ul li.active a,.profile-sidebar ul li a:hover{ background:#f4f8f6; color:#0ea092;}
.profile-sidebar ul li a i{padding-right:5px;}
a.closeSidebar:hover {
    color: #fff;
    box-shadow: none;
    background: #208d82;
}
a.closeSidebar {
    color:#fff;
    background: #0ea092;
    padding: 3px 10px;
    border-radius: 2px;
    border: 1px solid rgba(24, 168, 154, 0.53);
    margin-right: 12px;
    font-size: 21px;
    box-shadow: 0px 0px 5px 1px #c3c3c3;
    display: none;
}

@media (max-width:991px){
    .profile-sidebar{position:fixed; overflow:initial; z-index:9999;}
    .profile-sidebar .closeSidebar { position: relative; right: -145px; top: 31px;font-size: 19px; background: #0ea092; color: #fff; padding: 8px 13px; border-radius: 50%; box-shadow: 0px 0px 11px -2px #333; border: 1px solid #36827b; }
    .profile-sidebar:after{content:''; position: absolute; width:100%; height:100%;}
    a.closeSidebar { display:inline-block;}

}