.sidebar {
  background: #f9f9f9;
  position: absolute;
  padding: 0px 0 20px 0;
  z-index: 9;
  height: calc(100% - 220px);
  max-width: 400px;
  width: 100%;
}
.sidebar .title {
  background: #fff;
  padding: 20px 0 12px 72px;
}
.sidebar .filter {
  padding: 10px 30px 10px 72px;
  height: 100%;
  overflow: auto;
  background: #f9f9f9;
}
.sidebar .filter li {
  display: block;
  border-bottom: 1px solid #d2d2d2;
}
.sidebar .filter li a {
  display: block;
  padding: 5px 0 5px 0;
  color: #333;
  position: relative;
  text-align: left;
  font-size: 16px;
  text-transform: uppercase;
}
.sidebar .filter li a:hover {
  text-decoration: none;
  color: #22a689;
}
.sidebar .filter li a:after {
  content: '\F067';
  position: absolute;
  top: 10px;
  right: 10px;
  font-family: 'FontAwesome';
  font-size: 9px;
  padding: 0;
  border: 1.5px solid #22a689;
  height: 14px;
  width: 14px;
  line-height: 12px;
  color: #22a689;
  text-align: center;
}
.sidebar label.form-inline {
  font-weight: 600;
  font-size: 12px;
  margin: 0;
  color: #22a689;
}
.sidebar .dropdown-menu11 {
  padding: 7px 0;
}
.react-datepicker__input-container input {
  border: none;
}
.sidebar .react-datepicker__input-container input {
  width: 100%;
  border: none !important;
}
.sidebar i.fas.fa-calendar-alt {
  position: absolute;
  right: 21px;
  top: 6px;
  opacity: 0.4;
}
.sidebar .filter li a.active:after {
  content: '\f068';
}

@media screen and (max-width: 1380px) {
  .sidebar .title {
    text-align: left;
    font-size: 25px;
  }
  .sidebar {
    max-width: 300px;
  }
  .sidebar .title,
  .sidebar .filter {
    padding: 20px 15px 12px 15px;
  }
}

@media screen and (max-width: 991px) {
  .sidebar {
    left: -300px;
  }
  div#sidebar.active {
    left: 0;
    box-shadow: -7px 8px 36px 1px #212121;
  }
}

@media screen and (max-width: 579px) {
  .sidebar {
    max-width: 260px;
  }
  div#wrapperIdContent.active .setting {
    left: 259px;
  }
}
