
.rightSideBar { max-width: 350px; position: absolute; top: 0; width:100%; right: -15px; background: #f4f8f6; padding: 20px;top:75px; height:calc(100% - 100px); }
.sidebar-category{text-align:left;}
.sidebar-category p{font-size:14px;}
.sidebar-category h2{margin:20px 0 12px; color:#11a18c;}
.sidebar-category ul li a{display:block; color:#748c8e; font-size:20px; margin:12px 0 10px; position: relative;}
.sidebar-category ul li a .badge{    position: absolute; right: 12px; top: 4px;  width: 23px; height: 23px; line-height: 17px; font-size: 13px; border-radius: 50%; background: #748c8e;}

.rightSideBar .btn-filter {
    border: 1px solid #d4d4d4 !important;
    color: #1ca48a !important;
    background: #fff !important;
}


@media screen and (max-width:1380px){
    .rightSideBar { max-width: 270px;}
    .sidebar-category h2 { font-size: 25px;}
    .sidebar-category ul li a { font-size: 16px;}
    .sidebar-category ul li a .badge{font-size: 11px;}
}

@media screen and (max-width:991px){
   .rightSideBar{ position: relative;}
  .blog-cotainer .blog-list{width:100%;}
}
