.hotlines .search-panel{ padding:30px 0 0; background:#f4f8f6;}
.hotlines .advance-searh a{ color:#0ea092 !important}
.hotlines .breadcum{padding:6px 0; background-color:#f4f8f6;}
.hotlines .breadcum ul{margin:0;}
.hotlines .breadcum ul .nav-links{color: #6d6a6a;}
.therapy-content { margin-bottom: 70px;}
.hotlines .breadcum ul .nav-links::after{color: #0ea092;}
.intro-therapy{padding:60px 0 40px;}
.hotlines .content p{color:#7c8482;}
.hotlines .content{margin-bottom:30px;margin-top:40px;}
.hotlines .App-section .content p{ color: #fff;}
.hotlines .top-banner {  margin-top: -17px;}
.hotlines h4 {color:#7c8482; font-size:22px;}
.hotlines h4 small{font-size:12px;}

@media screen and (max-width:991px){
    .therapy-content p{font-size:15px; line-height:24px;}
    .hotlines .top-banner h1 { font-size: 40px;}
      .hotlines h4 {font-size:18px;}
    .hotlines .App-section .content h1 { font-size: 25px;}
   .hotlines .App-section .content img { max-width:120px; }
}
@media screen and (max-width:479px){
    .hotlines .top-banner h1 { font-size: 30px;}
    .hotlines h4 {font-size:16px;}
    .therapy-content h3 { font-size: 20px;}
    .intro-therapy { padding: 20px 0 10px;}
    .hotlines .content { margin-bottom: 20px;  margin-top: 27px;}
}