.workit .media {  margin: 102px auto 35px;   position: relative;}
.workit .media-left {  margin-right: 60px;}
.workit .media-left figure{width: 132px;text-align: center;height: 132px;display: flex;align-items: center;background: #f4f8f6;align-content: center;justify-content: center;border-radius: 50%;position: relative;z-index: 999;}
.workit .media-body::after{    content: '';position: absolute;height: 100%;width: 2px;background: #f4f8f6;top: 86px;left: 68px;}
.workit .media:last-child .media-body::after{display:none;}
.workit .media-body p{font-size:16px; font-weight:lighter; }
.workit{padding:83px 0;}
.workit h1.title{ font-size:46px;}
.workit h4.title{ margin-bottom:12px;}



@media screen and (max-width:1200px){
    .workit h1.title{ font-size:30px;}
    .sub-title { font-size: 20px;}
    .workit {  padding: 38px 0;}
    .workit .media {  margin: 54px auto 35px;   position: relative;}
}



@media screen and (max-width:767px){
    .workit .media {display:block; text-align:center;}
    
    .workit .media-body::after {display:none;}
    .workit .media-body {text-align:center !important}
    .workit .media-left {display:block; text-align:center; margin:0;}
    .workit .media-left figure{margin:0 auto 40px;}
}
@media screen and (max-width:479px){
    .workit .media-body p{font-size:14px;}
    .workit .media-body h4{font-size:18px;}
}

