figure.mobile_demo { display: flex;  margin: 0;    margin-top: 104px;   padding-left: 20%;}
figure.mobile_demo img {width:100%; height:100%;}
.App-section .content{ text-align:left; margin-top:113px; padding-bottom:100px;}
.App-section .content a{color:#8cc975}
.App-section .content a:hover{color:#8cc975}
.App-section .content h1{    font-size: 43px;  margin: 12px 0;}
.App-section .content p{font-size:18px; color:#fff; line-height:normal;    font-weight: lighter;  }
.App-section .content p img{ margin-top:30px;    max-width: 150px; }
.App-section{    background-repeat: no-repeat;
    background-size: cover;}

    
@media (max-width:1200px){
    .App-section .content h1 {  font-size: 30px;}
    .App-section .content {  padding-bottom: 23px;}
}
@media (max-width:991px){
    figure.mobile_demo {  padding-left: 0%;}
   .app-banner .content h1 { font-size: 25px;}
   .app-banner .content p{ font-size: 16px;}
     .app-banner .content p img { margin-top: 6px !important;}
}

@media (max-width:767px){
    figure.mobile_demo img {max-width:320px; margin:0 auto;}
    .App-section .content { margin-top: 38px;    padding-bottom:40px;  text-align: center;}
    figure.mobile_demo { margin-top: 19px;}
 
}