.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-link {
  color: #61dafb;
}

.btn.btn-primary {
  background-color: #34a2d7;
  border-color: #34a2d7;
  color: #fff;
}
.btn.btn-primary:hover {
  background-color: #1b76a3;
  border-color: #34a2d7;
  color: #fff;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body .page-item.active .page-link {
  z-index: 1;
  color: #fff;
  background-color: #0ea08c;
  border-color: #18a38b;
}
body .pagination-sm .page-item:first-child .page-link {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
body .pagination-sm .page-item:last-child .page-link {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
body .pagination-sm .page-link {
  padding: 5px 13px;
  font-size: 18px;
  line-height: 1.5;
  margin-left: 2px;
  color: #15a28b;
  background: #f4f8f6;
}
.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.checkbox {
  position: relative;
  padding-left: 30px;
}
.checkbox span:before {
  content: '';
  position: absolute;
  height: 20px;
  width: 20px;
  border: 1.5px solid #ddd;
  left: 0;
  top: 1px;
}
.checkbox input[type='checkbox']:checked + span:after {
  content: '';
  position: absolute;
  height: 10px;
  width: 10px;
  top: 6px;
  left: 5px;
  background: #34a2d7;
}
.checkbox input {
  display: none;
}
.modal-popup .form-group {
  margin-bottom: 6px;
}
.modal-popup .form-control {
  height: 30px;
  font-size: 14px;
}
.modal-popup textarea.form-control {
  height: 60px;
}
.modal-popup {
  max-width: 650px;
  padding: 20px 40px 0;
}
.modal-popup .form-container {
  padding: 20px;
  background: #f4f8f6;
  margin-bottom: 12px;
}
.modal-popup p {
  font-size: 12px;
  margin-bottom: 6px;
}
.modal-popup h4 {
  font-size: 18px;
}
.modal-popup .btn {
  width: 100%;
}
.modal-popup .media-left figure {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-position: center;
  background-size: cover;
  margin-right: 26px;
  position: relative;
}
.modal-popup .media-left {
  position: relative;
}
.modal-popup .media-left .crt {
  position: absolute;
  bottom: 0;
  left: 22px;
  z-index: 9;
}
.ReactModal__Content {
  overflow: initial !important;
  padding: 0 !important;
}
button.close {
  top: -21px;
  position: absolute;
  z-index: 99999;
  color: #fff;
  text-shadow: none;
  font-weight: 100;
  font-size: 15px;
  right: 0;
  opacity: 1;
}
button.close:hover {
  color: #61dafb;
}
body.ReactModal__Body--open {
  overflow: hidden;
}
/* .footer-buttons{display:none;} */
.multi-step {
  padding-bottom: 100px;
}
.footer-buttons {
  position: absolute;
  /* bottom: 101px;
    margin-left: 53px; */
}
.calendar-heading button.fas.fa-angle-left,
.calendar-heading button.fas.fa-angle-right {
  background: transparent;
  border: 0;
  font-size: 40px;
  color: #fff;
}
.btn-success .fa {
  color: #fff;
}

[type='radio']:checked,
[type='radio']:not(:checked) {
  position: absolute;
  left: -9999px;
}
[type='radio']:checked + label,
[type='radio']:not(:checked) + label {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  color: #666;
}
[type='radio']:checked + label:before,
[type='radio']:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  border: 1px solid #ddd;
  border-radius: 100%;
  background: #fff;
}
[type='radio']:checked + label:after,
[type='radio']:not(:checked) + label:after {
  content: '';
  width: 12px;
  height: 12px;
  background: #18968a;
  position: absolute;
  top: 3px;
  left: 3px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
[type='radio']:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}
[type='radio']:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

.full-width {
  width: 100%;
}

.top-links-section a {
  color: #34a2d7;
}

@media (max-width: 991px) {
  button.close {
    top: 21px;
    position: initial;
    z-index: 99999;
    color: #34a2d7;
  }
}
@media (max-width: 767px) {
  .ReactModal__Content {
    overflow: auto !important;
    max-height: 100%;
  }
  .modal-popup {
    max-width: 100%;
    padding: 20px 15px 0;
  }
}
.imageUploadLabel {
  width: 100px;
  height: 20px;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #000000;
  cursor: pointer;
}
