.search-page {
  position: relative;
}
.search-page .top-search-panel {
  background: #22a689;
  vertical-align: middle;
  color: #fff;
}
.breadcum {
  margin: 0;
  padding: 10px 0;
}
.breadcum li {
  display: inline-block;
  padding: 0 30px 0 0;
  position: relative;
}
.breadcum li a:after {
  content: '\F054';
  position: absolute;
  right: 10px;
  font-family: 'FontAwesome';
  top: 15px;
  font-size: 11px;
}
.breadcum li:last-child a::after {
  display: none;
}
.breadcum li a {
  display: block;
  padding: 10px 0;
  font-weight: 100;
}
.top-search-panel a img {
  height: 20px;
}
.top-search-panel a {
  color: #fff;
}
.top-search-panel a:hover {
  text-decoration: none;
  color: #fff;
}
.nav-option li {
  position: relative;
  padding: 21px 20px;
}
.nav-option li.nav-link.active {
  background: #73bfb3;
  color: #fff !important;
}
.nav-option li span {
  display: block;
}
.nav-option li.nav-link.active::after {
  content: '\F0D7';
  position: absolute;
  bottom: -21px;
  color: #73bfb3;
  font-family: 'FontAwesome';
  font-size: 25px;
  left: 50%;
}
.nav-option li span::after {
  content: '';
  position: absolute;
  height: 100%;
  width: 0.5px;
  right: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.49019607843137253);
}
.nav-option li span::before {
  content: '';
  position: absolute;
  height: 100%;
  width: 1px;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.49019607843137253);
}
