.inner-wrapper {
  min-height: 600px;
  margin-left: 400px;
  padding: 20px 15px;
}
.search-list .media {
  margin-right: 6%;
  border-bottom: 1px solid #e6e6e6;
  padding: 30px 0;
}
.search-list .media-left figure {
  width: 180px;
  margin: 0 auto;
  height: 180px;
  position: relative;
  background-size: cover;
  background-position: center;
  margin-bottom: 75px;
  border-radius: 50%;
}
.search-list .media-left figure a {
  position: absolute;
  bottom: -15px;
  left: calc(50% - 12px);
}
.help-search{
  padding-bottom: 15px;
  padding-top: 15px;
}
.searchList-content h2 {
  color: #34a2d7;
  font-size: 34px;
  line-height: 28px;
}
.searchList-content p {
  color: #7c8482;
  font-size: 16px;
  margin: 0;
}
.searchList-content .app {
  background: #eff8fc;
  padding: 15px;
  max-width: 513px;
  border: 1px solid #e5f5fc;
  border-radius: 4px;
  margin-bottom: 39px;
}
@media (min-width: 1380px) {
  .searchList-content .app {
    margin-bottom: 20px;
  }
}
.searchList-content .app label {
  color: #34a2d7;
  font-size: 18px;
  font-weight: bold;
}
.searchList-content .app i {
  color: #34a2d7;
}
.searchList-content .rating {
  color: #ffd203;
  display: block;
}
.searchList-content .rating span {
  color: #7c8482;
}
.searchList-content .btn-primary {
  background-color: #34a2d7;
}
.searchList-content .btn-success,
.searchList-content .btn-success:hover,
.search-list .bnt-filter {
  background-color: #fff;
  color: #22a689;
  border-color: #22a689;
}
.searchList-content .btn-success:hover {
  background-color: #22a689;
  color: #fff;
}
.searchList-content {
  padding-left: 20px;
}
.search-list .media-right figure {
  margin-left: 12px;
  margin-bottom: 0;
  position: relative;
}
.search-list .media-right figure figcaption label {
  font-weight: bold;
  margin-bottom: 6px;
}
.search-list .media-right figure figcaption p {
  margin-bottom: 6px;
  font-size: 17px;
  line-height: 18px;
  color: #4f5654;
}
.setting {
  display: none;
}
.paggination {
  padding: 21px 0;
}
.setting i {
  -webkit-animation: rotation 2s infinite linear;
  -moz-animation: rotation 2s infinite linear;
  -o-animation: rotation 2s infinite linear;
  animation: rotation 2s infinite linear;
}
@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
  }
}
.setting:hover,
.setting:focus {
  color: #0a7b6c;
}
.map-marker-container {
  position: relative;
  min-height: 600px;
  width: 100%;
}
.rangeSlider {
  display: inline-block;
  width: calc(100% - 190px);
  position: relative;
  padding: 6px 12px;
  vertical-align: middle;
}
article.mapSearch {
  display: flex;
  padding: 12px 72px;
}
article.mapSearch .Address {
  color: #22a689;
  font-size: 25px;
  padding-right: 28px;
}
article.mapSearch .form-group {
  width: 100%;
  margin: 0;
  position: relative;
}
article.mapSearch .form-group .form-control {
  width: 100%;
}
article.mapSearch .form-group span {
  position: absolute;
  right: 12px;
  top: 4px;
}

