/* End */

@media screen and (max-width: 1380px) {
  .inner-wrapper {
    margin-left: 300px;
  }
  .searchList-content h2 {
    font-size: 25px;
  }
  .searchList-content .app label {
    font-size: 14px;
  }
  .searchList-content p {
    font-size: 14px;
  }
  .search-list .media {
    margin-right: 15px;
  }
  .search-list .media-right figure figcaption p {
    font-size: 15px;
    line-height: 16px;
  }
  .search-list .media-right figure {
    max-width: 200px;
    width: 100%;
  }
}

@media screen and (max-width: 991px) {
  /* .sidebar{left:-300px;} */
  .inner-wrapper {
    margin-left: 0px;
  }
  .search-list .media {
    margin-right: 0;
    margin-bottom: 0;
  }
  .nav-option li {
    position: relative;
    padding: 10px 9px;
  }
  .breadcum li {
    padding: 0 19px 0 0;
  }
  .breadcum li a:after {
    right: 4px;
    opacity: 0.7;
  }
  .setting {
    display: block;
  }
  /* div#sidebar.active { left: 0;  box-shadow: -7px 8px 36px 1px #212121;} */
  a.setting {
    position: absolute;
    left: 0;
    top: 53px;
    width: 46px;
    text-align: center;
    height: 43px;
    line-height: 45px;
    background: #eff8fc;
    font-size: 25px;
    color: #22a689;
    box-shadow: 1px 1px 1px 1px #c0d8e47d;
    border: 1px solid #c6d9e2;
    cursor: pointer;
  }
  div#wrapperIdContent.active .setting {
    left: 299px;
  }
  article.mapSearch {
    display: flex;
    padding: 12px 15px;
    padding-left: 62px;
  }
}

@media screen and (max-width: 767px) {
  .search-list .media {
    display: block;
  }
  .searchList-content .app {
    max-width: 412px;
    margin: 12px auto;
  }
  .search-list .media-right figure {
    max-width: 320px;
    margin: 0 auto 0;
  }
  .searchList-content .btn-primary,
  .searchList-content .btn-success {
    margin-bottom: 15px;
  }
  .searchList-content {
    text-align: center;
  }
  .nav-option li a::before {
    display: none;
  }
  .nav-option {
    background: #0a7b6c;
    margin-left: -15px;
    margin-right: -15px;
    justify-content: center;
  }
  a.setting {
    top: 90px;
  }
}

@media screen and (max-width: 579px) {
  a.setting {
    top: 130px;
    z-index: 99;
  }
  /* .sidebar { max-width: 260px;} */
  div#wrapperIdContent.active .setting {
    left: 259px;
  }
}
