
.search-panel .location .form-control, .search-panel .timeSpam .form-control {  border-radius: 0 !important;}
.search-panel .btn-group .comments {  width: 208%;}
.input-group-text {  background-color: #fff !important;  border-color: #fff !important;   border: 0 !important;    border-right: 1px solid #adadad !important;    color: #7c8482;   }
.search-panel .form-control:focus{outline:none;     box-shadow: none; border-color:#fff;} 
.search-panel  .input-group-prepend{margin:0;}
.search-panel  input.form-control {  border: 0;}
.search-panel .form-control::placeholder, .search-panel .form-control::-webkit-placeholder,.search-panel .form-control::-moz-placeholder,.form-control::-o-placeholder,.search-panel .form-control{ color: #7c8482; }
.search-panel   button.btn.btn-success {  background-color: #fff;  color: #1ba48a;   padding: 6px 36px;  font-size: 20px;   border: 1px solid #fff;}
.advance-searh{padding: 25px 0 0;}
.search-panel .btn-group {  border-radius: 6px;  width:100%;    border: 2px solid #ececec;}
.search-panel button.btn.btn-success:focus,.search-panel button.btn.btn-success:hover, .search-panel button.btn.btn-success:active{ box-shadow:none; outline:0; background-color:#1ba48a; color:#fff; }
 .search-panel {  padding: 0 15px 6px;}
.search-panel .input-group-prepend i { color: #d5d5d5;}
p.text-center.advance-searh a {  font-size: 20px;}
.search-panel .btn-group .input-group{background-color:#fff;}







@media screen and (max-width:767px){
    .search-panel .input-group-text {  border-right: 0 !important;}
    .search-panel .btn-group{ flex-direction:column;}
    .search-panel .btn-group .input-group{width:100%; margin-bottom:12px;}
    .search-panel input.form-control { border-radius: 0; height:46px;}
    .search-panel button.btn.btn-success { border-radius: 0;}
    .search-panel .btn-group { background:transparent;  border-radius: 0;  border: 1px solid transparent;}
}



