.blog-page .search-panel {    padding: 37px 0 0;    background: #f4f8f6;}
.blog-page .advance-searh a {    color: #11a18c !important;}
.blog-page {position: relative;}
.bread-cum {  text-align: left;     padding: 9px 72px 30px;}
.blog-cotainer {  position: relative;    margin-bottom: 100px;}
ul.social-links,.blog-page .run-as,.blog-page .breadcum, .blog-page .title{display: inline-block;}
.run-as{float:right; vertical-align:middle;}
ul.social-links li{ display: inline-block;}
.blog-page .breadcum li a{color:#333;}
.blog-page .breadcum li a:after,.blog-page .breadcum li{color:#11a18c;}
ul.social-links li .blue{ background-color:#305890;}
ul.social-links li .sky{ background-color:#2ca9d2;}
ul.social-links li .orange{ background-color:#fb6f4e;}
ul.social-links li a{ display: block; height: 34px; width: 34px; text-align: center; line-height: 34px; background: #748c8e; margin-left: 5px;color: #fff;}
.blog-page .title{padding-right:20px;}
.blog-list figure{ height: 100%; background-position: center; background-size:cover; background-repeat: no-repeat; background-color:#ddd; }
.blog-list {  width: calc(100% - 390px); padding-left:72px; }
.blog-list .blog-content{text-align:left;}
.blog-list .blog-content .blog-title{font-size:15px; font-weight:500; color:#11a18c; }
.blog-list .blog-content .blog-subtitle{font-size:21px; font-weight:lighter; color:#34a2d7; }
.blog-list .blog-content p{font-size:15px; color:#7c8482; }
.blog-list .blog-content a{font-size:16px; color:#32ad96; font-weight:bold; }
.blog-list-wrapper figure { height: 175px;  width: 100%;}
.blog-list-wrapper .items{margin-top:50px;}
.blog-list-wrapper a:hover{text-decoration:none;}

@media screen and (max-width:1380px){
    .bread-cum{padding: 9px 15px 30px;}
    .blog-list{padding-left: 15px;}
    .blog-list { width: calc(100% - 270px);}
}

@media screen and (max-width:991px){
     .blog-list figure{min-height: 175px;}
    .run-as { width: 100%;  float: none;  text-align: right;  padding: 4px 0;}
}
@media screen and (max-width:767px){
    .rightSideBar { max-width: 100%; margin: 0;  position: inherit;}
}




