.profile .top-search-panel{ background-color:#0b9f8d;    padding: 0 63px;}
.profile {  min-height: 653px;}
.profile .top-search-panel .social-links{margin-bottom:0;    margin-top:15px;}
.profile .top-search-panel .social-links a{background-color: rgba(255, 251, 251, 0.5803921568627451);color: #0b9f8d;padding: 0; height: 28px;width: 28px;border-radius: 4px;vertical-align: middle;}
.therapist-profile{padding:34px 76px 60px;}
.intro{width: calc(100% - 300px);   margin-bottom: 22px;}
.intro h2{font-size:45px; margin:0;}
.react-tabs__tab { display: inline-block; padding: 10px 45px;  border-right: 6px solid #fff;}
li.react-tabs__tab.react-tabs__tab--selected { background: #91cc7d; color: #fff;}
ul.react-tabs__tab-list {  background: #eff0f0;}
li#react-tabs-0, li#react-tabs-0 a { background: #34a2d7;  color: #fff;}
.tag-list .badge{    background: #dbf1ee; padding: 10px 15px; margin-right: 10px; color: #0b9f8d;    margin-bottom: 6px;}
.qualifications label{font-weight:bold; color:#7c8482; font-size:18px;}
.qualifications .col-6{font-weight:lighter;}
.treated {margin-top: 30px;    width: 100%;  padding: 50px 0; margin-top: 30px;  border-top: 1px solid #ddd;}
.qualifications { padding: 36px 0 20px;  border-top: 1px solid #ddd;}
.image-container figure{ height:120px; width:120px;display: inline-block;  margin-right: 12px;}
.expert{position: absolute;right: 130px;top: 48px;max-width: 300px;width: 100%;text-align: center;padding: 20px 30px; transition:right 0.5s ease;}
.expert .btn.btn-primary{ background-color:#34a2d7; width:100%; margin-bottom:12px; height:43px; }
.expert .btn.btn-primary:hover{ background-color:#34a2d7;}
.expert .btn.btn-filter{ width:100%; border:1px solid #0b9f8d; color:#0b9f8d; font-size:18px;  }
.expert button{ height:43px; width:100%; margin-bottom:12px; }
.expert figure{height: 200px; width: 200px; margin: 0 auto; background-size: cover; border: 6px solid #fff; margin-bottom: 40px;    border-radius: 50%;}
.expert figcaption{background-color:#fff;}
.treated ul {  list-style: disc;  padding-left: 20px;}
.therapist-profile {  position: relative;}
.therapist-profile>a.text-primary{font-size:18px;}
.therapist-tab { width: calc(100% - 300px);margin: 22px 0 0;}
.therapist-tab .react-tabs__tab-list {  margin-bottom: 49px;}
.insurance .fee {  padding: 56px 0 0;}
p.rating i { color: #ffd203;}
.profile .therapy-type h4{color:#34a2d7; font-size:24px;}
.profile .therapy-type p{color:#7c8482; font-size:18px; font-weight:300;}
.profile .therapy-type .form-group{width:90%; margin-top:30px; padding-bottom:20px; border-bottom:1px solid #ddd; }
.profile .therapy-type .form-group:last-child{border:0;}
.expert .therapist-toggle {position: absolute;left: -40px;top: 54px;width: 40px;height: 40px;line-height: 40px;background: #0b9f8d;color: #fff;font-size: 25px;border-top-left-radius: 12px;border-bottom-left-radius: 12px;box-shadow: -2px 0px 6px -1px #a7a7a7;    z-index: 999;display: none;}
.fee .table-responsive{ max-width:1024px; }
.fee .table thead{ background-color:#0b9f8d; border-radius:9px; }
.fee .table thead th{color:#fff; position: relative;}
.fee .table thead th:first-child span{}
.fee .table tbody tr td{color:#7c8482; font-size:20px;}
.fee .table tbody tr:nth-child(even){background-color:#f5f6f6;}
.fee .table tbody tr td:first-child{font-weight:bold;}
.fee .table thead th:last-child,.fee .table tbody tr td:last-child{text-align:right;}
.fee .table thead th:last-child{padding-right:80px;}
.fee .table thead th:first-child,.fee .table tr td:first-child{padding-left:40px;}
.fee .table td:last-child{padding-right:60px;}
.table {min-width:500px;    border-collapse: collapse;    border-radius: 7px;    overflow: hidden;}
li#react-tabs-10.react-tabs__tab--disabled { background: #34a2d7; color: #fff;}
li#react-tabs-10.react-tabs__tab--disabled a{color: #fff;}
li#react-tabs-10.react-tabs__tab--disabled a:hover{text-decoration:none;}



@media (max-width:1480px){
    .intro h2{    font-size: 30px; margin:0;}
    .react-tabs__tab{    padding: 10px 35px;}
}

@media screen and (max-width:1380px){
    .intro ,.therapist-tab{  width: calc(100% - 250px);}
    .expert { right: 79px;}
    .profile .therapy-type h4 { font-size: 20px;}
    .profile .therapy-type p { font-size: 16px;}
    .intro h2{font-size:30px;}
    .react-tabs__tab{    padding: 10px 20px;}
}
@media screen and (max-width:1200px){
    .react-tabs__tab { display: inline-block; padding: 8px 15px;  border-right: 3px solid #fff;}
    ul.react-tabs__tab-list {    background:transparent;}
    .react-tabs__tab{background-color:#eff0f0; margin-bottom:5px;}
    .intro h2{font-size:25px;}
    .expert {  right: 31px;}
    .profile .top-search-panel{padding:0 15px;}
    .therapist-profile {  padding: 40px 15px 60px;}
      .react-tabs__tab{    padding: 10px 30px;}
}

@media screen and (max-width:991px){
  .expert{right:-280px; position: absolute;width: 271px;}
  .expert.open {
    right: 0;
    background: #f9f9f9;
    border: 1px solid #dedede;
    
}
  .intro, .therapist-tab{width: 100%;}
  .profile .therapy-type .form-group{width:100%;}
    .profile { min-height: 653px;  overflow: hidden;}
    .expert .therapist-toggle { display: block;}
   
}

@media screen and (max-width:767px){
    .profile .text-right {  text-align:left !important;}
    .profile .top-search-panel .social-links { margin-bottom: 8px;  margin-top: 4px;}
    .map-container{width:100%;overflow:hidden;}
    .qualifications{overflow: hidden;}
}
 




