.support .search-panel{ padding:30px 0 0; background:#f4f8f6;}
.support .advance-searh a{ color:#0ea092 !important}
.support .breadcum{padding:6px 0; background-color:#f4f8f6;}
.support .breadcum ul{margin:0;}
.support .breadcum ul .nav-links{color: #6d6a6a;}
.therapy-content { margin-bottom: 70px;}
.support .breadcum ul .nav-links::after{color: #0ea092;}
.intro-therapy{padding:60px 0 40px;}
.support .content p{color:#7c8482;}
.support .content{margin-bottom:30px;margin-top:40px;}
.support .App-section .content p{ color: #fff;}
.support .top-banner {  margin-top: -17px;}
.support .accordion .card-header .btn, .support .accordion .card-header h5::before{color: #0ea092; }
.support .accordion .card-header.active .btn, .support .accordion .card-header.active h5::before{color: #fff; }
.support .faq-content>.title{    margin-bottom: -30px;}
@media screen and (max-width:991px){
    .therapy-content p{font-size:15px; line-height:24px;}
    .support .top-banner h1 { font-size: 40px;}
    .support .App-section .content h1 { font-size: 25px;}
    .support .App-section .content img { max-width:120px; }
}
@media screen and (max-width:479px){
    
    .support .top-banner h1 { font-size: 30px;}
    .therapy-content h3 { font-size: 20px;}
    .intro-therapy { padding: 20px 0 10px;}
    .support .content { margin-bottom: 20px;  margin-top: 27px;}
    .support .accordion .card-header h5 button { white-space: inherit;  word-break: break-word;}

}