/* .accordion{ margin:50px 0 } */
.accordion .card-header.active{ background: #1ba48a; color:#fff; }
.accordion .card-header{  border-radius:6px !important;   padding: 3px 34px 0 0; position: relative; background: #fff; text-align: left; margin-top: 12px;border: 1px solid #ddd;}
.accordion .card-header.active h5 button {    color: #fff;
    height: 50px;
    font-size: 22px;
    padding-left: 34px;
    letter-spacing: 0.6px;}

    .accordion {
    max-width: 965px;
    margin: 30px auto;
    padding-bottom: 96px;
}
.accordion .card-header h5 button {  height: 50px;
    font-size: 22px;
    color:#7c8482;
    padding-left: 34px;
    letter-spacing: 0.6px;}
.accordion .card-header h5::before{content: "\F107"; font-family: "FontAwesome";  color: #545b59; border: 0 !important;  right:15px;  position: absolute;    top:12px;}
.accordion .card-header.active h5::before{content: "\f106"; color:#fff;    }
.accordion .card-header h5 button:hover {text-decoration:none;}
.accordion .card{border:0;}
.accordion .card .card-body{    text-align: left;
    color: #7c8482;
    font-size: 18px;
    padding: 31px 30px 26px; }
.faq-content{padding:83px 0 0}
.faq-content h1.title{    font-size: 46px;}



 @media screen and (max-width:1200px){
   .faq-content h1.title{    font-size: 30px;}
   .accordion {  margin: 8px auto; padding-bottom:40px;}
   .accordion .card-header.active h5 button { height: 43px; font-size: 19px;}
   .accordion .card .card-body { color: #7c8482; font-size: 16px;  padding: 26px 30px 17px;}
   .accordion .card-header h5 button { height: 43px;  font-size: 18px;}
 }
 @media screen and (max-width:991px){
   .faq-content .title{font-size: 30px;}
   .accordion .card .card-body {font-size: 15px;}
   .faq-content { padding: 29px 0 39px;}
   .faq-content h1.title{font-size: 22px;}
 }