
@import url('https://fonts.googleapis.com/css?family=Hind+Madurai:300,400,500,600,700&subset=latin-ext,tamil');


body {
  margin: 0;
  padding: 0;
 font-family: 'Hind Madurai', sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
 font-family: 'Hind Madurai', sans-serif;
}
.title, .active{color:#1ba48a !important;}
ul{padding: 0; margin:0; list-style:none;}
img{max-width:100%;}

.dropdown-toggle::after{
    content: "\F107" !important;
    font-family: "FontAwesome" !important;
    border: 0 !important;
    margin-top: -5px;
    position: relative;
    top: 5px;
}
p{color:#7c8482;}

.sub-title{font-size:25px; color:#1ba48a; font-weight:lighter;}

::-webkit-scrollbar {
    width: 10px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgb(158, 158, 158);
}
 
::-webkit-scrollbar-thumb {
 /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#6cb257+1,1ca48a+100 */
background: rgb(108,178,87); /* Old browsers */
background: -moz-linear-gradient(top, rgb(108,178,87) 1%, rgb(28,164,138) 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top, rgb(108,178,87) 1%,rgb(28,164,138) 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom, rgb(108,178,87) 1%,rgb(28,164,138) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6cb257', endColorstr='#1ca48a',GradientType=0 ); /* IE6-9 */
  outline: 1px solid slategrey;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #126b5b;
  outline: 1px solid slategrey;
}
body .text-primary{ color:#34a2d7 !important; }

.ReactModal__Overlay {
    background-color: rgba(56, 111, 111, 0.81) !important;
    z-index: 999;
}