.contact-page .search_box {  padding: 37px 0 0;  background: #f4f8f6;}
.contact-page .advance-searh a{ color:#11a18c !important; }
.contact-page .cform{ max-width: 981px;width: 100%;margin: 43px auto 80px;padding: 48px 55px;background: #f4f8f6;border-radius: 51px;}
.contact-page .cform .form-control, .contact-page .cform select.form-control{height:50px !important;}
.contact-page .form-control:hover, .contact-page select.form-control:hover, .contact-page textarea.form-control:hover,
.contact-page .form-control:focus, .contact-page select.form-control:focus, .contact-page textarea.form-control:focus{ outline:none; box-shadow:none;}
.contact-page textarea.form-control{min-height: 168px;}
.contact-page .cform .btn{margin: 4px 0;  height: 47px;  max-width: 215px;  width: 100%;}
.contact-page .contact-form{padding:80px 0 0;}
.contact-page .form-inline,.contact-page select{color:#7c8482;}
.contact-page select{-webkit-appearance:none;-moz-appearance:none;appearance:none;background-repeat: no-repeat;background-position: 97% center;
background-size: auto;}

@media (max-width:1200px){
    .contact-page .contact-form {  padding: 36px 0 0;}
    .contact-page .cform {  margin: 40px auto 33px;  padding: 48px 32px;   border-radius: 14px;}
    .contact-page .cform .form-control, .contact-page .cform select.form-control { height: 41px !important;}
    .contact-page textarea.form-control { min-height: 86px;}
}

@media (max-width:479px){
    .contact-page .cform {  padding: 48px 15px;}
}


