.login{background: #fff;  position: absolute; height: calc(100% + 30px);  width: 430px;  margin: 0 auto; left: 0;  right: 0;   z-index: 9;top: -11px;bottom: -11px;box-shadow: 0px 0px 3px 1px #d2d2d2;     display: flex;
 align-content: center; justify-content: center; align-items: center;}
.login-panel {  background-size: cover;}
article.ribban { position: absolute; top: 0;  right: 18px;}
section.logcontent.text-left { max-width: 320px; width: 100%;}
.signup.text-left { color: #fff;  padding: 67px 0px;}
.signup.text-left small{    letter-spacing: 5px;}
.signup.text-left p{color:#fff;}
.signup h1{margin-bottom:0px;}
.signup p{margin-bottom:31px; font-weight:lighter; }
.signup .btn-primary, .signup .btn-danger{     width: 100%;
    border-radius: 0;
    text-align: left;
    margin-bottom: 51px;
    box-shadow: none;
    outline: 0;
    height: 47px;
    font-size: 21px;}
.signup .btn-primary i, .signup .btn-danger i{ padding-right:12px;    font-size: 25px;  vertical-align: middle; }
.signup .btn-primary{background-color:#3f5e9e}
.signup .btn-danger{background-color:#ee4a3a}
button.btn.btn-filter {  background: transparent;  border: 2px solid #fff;  color: #fff;  font-size: 20px; max-width: 254px; width: 100%;}
.login small{color:#adb9b7; letter-spacing:1px; }
.login .btn{ background-color:#1ba48a; width:100%; }
button.btn.btn-filter:hover,.login .btn:hover, .login .btn:focus{ background-color:#148570; width:100%; color:#fff; }
.signup .or{ content: ''; position: absolute; height: 20px; top: -36px; width: calc(100% - 30px);}
button.my-facebook-button-class.metro .fa-facebook{ margin-right:12px;}
button.my-facebook-button-class.metro {
    background: #3f5f9c;
    color: #fff;
    border: 0;
    height: 43px;
    padding: 0 12px;
    margin-bottom: 41px;
    font-size: 18px;
    border-radius: 2px;
    box-shadow: 1px 1px 1px 1px #00000042;
    widows: 100%;
}
my-facebook-button-class {
    background: #3f5f9c;
    background-color: #3f5f9c;
    color: #fff;
    border: 0;
    height: 43px;
    padding: 0 12px;
    margin-bottom: 41px;
    font-size: 18px;
    border-radius: 2px;
    box-shadow: 1px 1px 1px 1px #00000042;
    widows: 100%;
}

@media screen and (max-width:991px){
    .login{position: relative; top:0;width:100%; height:100%; z-index: 9; padding-left:15px; padding-right:15px;  }
    .signup h1 {  font-size: 25px;}
    section.logcontent.text-left h1 {  font-size: 25px;}
    .signup p {  margin-bottom: 14px;  font-weight: lighter;   font-size: 15px;}
    .signup .btn-primary i, .signup .btn-danger i { padding-right: 3px; font-size: 20px;}
    .signup .btn-primary, .signup .btn-danger{    font-size: 12px;}
    button.my-facebook-button-class.metro {     height: 37px; font-size: 10px;}
    .google_login button span {  font-size: 10px;}
    .google_login button div {margin:0 !important}
  .google_login button { height: 39px; line-height: 7px;}
}

@media screen and (max-width:767px){
    .login{position: relative; top:0; margin:50px auto 50px; width:100%; z-index: 9;  }
    button.my-facebook-button-class.metro {
    height: 46px;
    font-size: 18px;
}.google_login span {
    font-size: 16px;
}
.google_login {
    height: 43px;
    line-height: 26px;
    margin-bottom: 42px;
    background: #ee4a3a;
    background-color: #ee4a3a;
    color: #fff;
    width: 100%
}
}
@media screen and (max-width:580px){
.signup .btn-primary{margin-bottom:12px;}
}

